body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/********** Global App Styling **********/

.center {
  text-align: center;
}

/*Add space above and below the heading on the About Component*/
.rowWhiteSpace {
  margin-top: 10px;
  margin-bottom: 10px;
}

/*White-space*/
.whiteSpaceAboveFeatureArea {
  margin-top: 2rem;
}

.appBackGroundColor {
  background-color: #eee9e9;
}

/********** End of Global App Styling **********/


/********** Nav Component Styling **********/
.navSection {
  background-color: rgb(32, 179, 199);
  display: flex; /* Affect content of this container*/
  align-items: center; /*Center the content on the x axis*/
  justify-content: space-around; /*Center the content on the y axis*/ 
  margin-bottom: 50px;
}

.navButton {
  color: white;
}

/********** End of Nav Component Styling **********/

/********** Intro Component Styling **********/

/*Style for the entire Intro Container*/
.introContainer {
  min-height: 80vh; /*made the container the height of the screen window*/

}

/*Style for the text content in Intro*/
.introTitle {
  text-align: center;
  color: white;
  z-index: 1;
}

/*Make my name bigger*/
.enlargeTitle {
  font-size: 4em;
}

/*
Bug fix: Was using a dark overlay and z-index for text to darken image but show text on top but it 
really wasn't working. The fix was to use a linear-gradient. 
*/
.introImage {
  background-image: -webkit-gradient( linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5)) ),url(/v2-Portfolio/static/media/intro_meetup.7990ea04.jpg);
  background-image: -webkit-linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ),url(/v2-Portfolio/static/media/intro_meetup.7990ea04.jpg);
  background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ),url(/v2-Portfolio/static/media/intro_meetup.7990ea04.jpg);
  background-size: cover;
  background-position: center center;
  overflow: hidden;
  background-attachment: fixed;
  display: flex; /* Affect content of this container*/
  align-items: center; /*Center the content on the x axis*/
  justify-content: center; /*Center the content on the y axis*/  
}

@media only screen and (max-width:425px){
  /*Show a different background picture in mobile view on the intro component*/
  .showMobileImage {
    background-image: -webkit-gradient( linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5)) ),url(/v2-Portfolio/static/media/meetup-me-standing-small.bfe60c50.jpg);
    background-image: -webkit-linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ),url(/v2-Portfolio/static/media/meetup-me-standing-small.bfe60c50.jpg);
    background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ),url(/v2-Portfolio/static/media/meetup-me-standing-small.bfe60c50.jpg);
  }
}


@media only screen and (min-width:426px){
  /*Increase size of social media links on intro page in large views*/
  .introSocialMediaLinks {
    font-size: 1.5rem;
  }
}

.introTitle > h1 , .introTitle > h3, .introTitle > h5 {
  margin-bottom: 3vh ;
}

/*Ensure social media icons/links look like icons*/
.introSocialMediaLinks {
  color:white;
}

/*white space between feature's project title and image*/
.whiteSpaceBelowText {
  margin-bottom: 1rem;
}

.imageShadowBorder {
  box-shadow: 1px 2px 5px 2px rgb(159, 166, 160); /*shadow under the button*/
}

/********** End Intro Component Styling **********/


/********** About Component Styling **********/

/*Style for Sub-Title on About Component*/
.greyColor {
  color: grey;
  margin-bottom: 10px;
}

.aboutCards {
  box-shadow: 1px 2px 5px 2px rgb(159, 166, 160);
}

/*Make text smaller on smaller screens*/
@media screen and (max-width:990px){
  .aboutCardCols {
    margin-bottom: 1.5rem;
  }  
}

/*Center align the about me text*/
.verticalAlignText {
  margin: auto;
  width: 50%; 
  text-align:left;
}

@media only screen and (max-width:426px){
  /*Increase whitespace around head-shot on the about page*/
  .aboutPicSmallView {
    margin-top: 3rem;
  }
}

/********** End of About Component Styling **********/


/********** Portfolio Component Styling **********/
.spaceBetweenPortfolioCards {
  margin-bottom: 2vh;
}

.equalTitleHeight {
  height: 10vh;
  display:flex;
  justify-content: center;
  align-items: center;
}

.equalImgHeight {
  height: 60vh;
  border-top: .1rem solid lightgrey;
  border-bottom: .1rem solid lightgrey;
}

.equalTechHeight {
  height: 15vh;
}

.equalObjHeight-WIP {
  height: 15vh;
  padding-bottom: 10vh;  
}

/*smaller text for Node/Express to make it fit */
.smallText {
  font-size: .8rem;
}  

/*Make text smaller on smaller screens*/
@media screen and (max-width:470px){
  .allTextSmall {
    font-size: .8rem;
  }
}

/*Make text smaller on smaller screens*/
@media screen and (min-width:768px) and (max-width:986px){
  .equalImgHeight {
    height: 70vh;
  }  
}

/*CSS for bigger screens*/
@media screen and (min-width:987px){
  .equalTechHeight {    
    padding-bottom: 1vh;
  }
  
  .equalObjHeight {
    height: 10vh;
    /*padding-bottom: 30vh;*/
  }

  /*To fix bug on Edge Motors title text is overflowing the white box*/
  .equalTitleHeight { 
    height: 10vh;
  }
}
/********** End of Portfolio Component Styling **********/

/********** Start of Footer Component Styling **********/
.footerStyling {
  background-color: rgb(32, 179, 199);
  margin-top: 1rem;
}

/********** End of Footer Component Styling **********/
